import React from 'react';

import { Input, Button } from 'reactstrap';

import image1 from '../../../assets/images/totem-image-1.png';
import image2 from '../../../assets/images/totem-image-2.png';
import image3 from '../../../assets/images/totem-image-3.png';

import style from './style.module.scss'

function ImageGallery() {

    return (<>
        <div className={style.page}>
            <div className={style.clientContainer}>
                <h2>It was a pleasure working with the team at Totem. 
                    The team is friendly, collaborative, and adaptive. 
                    Moreover, each team member puts a lot of thought 
                    into the learning experience and continually makes 
                    an effort to simplify and clarify key points of 
                    learning. In the end, Totem delivered two learning 
                    modules that we believe are polished, intuitive, 
                    and will be effective resources for the organization. 
                    In a word - Outstanding. Thank you.</h2>
                    <p className={style.subtitle}>JOSEPH THERIAULT - SENIOR MANAGER - STRATEGIC FINANCE AND ANALYTICS - MARS</p>
            </div>
            <br/>

            <div className={style.clientContainer}>
                <h2>We have collaborated on two projects in Shell and both times I have been blown away by the inventiveness, eagerness and flexibility shown by all the team. It’s very hard to identify what it is that you do better than your competitors - it’s a certain something that you can’t put your finger on... which is great because it’s hard to replicate.</h2>
                    <p className={style.subtitle}>ANDY GOOSE, SHELL UK</p>
            </div>
            <br/>

            <div className={style.clientContainer}>
                <h2>Thank you, to all of you. I really believe we are onto something. I’m both excited and serene that we have a good partner in Totem and the individuals who have come together through it.</h2>
                    <p className={style.subtitle}>DR DES HOLDEN, MEDICAL DIRECTOR, NHS ENGLAND</p>
            </div>
            <br/>

        </div>
        </>);
}

export default ImageGallery;
