import React from 'react';

import { Col, Row } from 'reactstrap';

import image1 from '../../../assets/images/totem-great-1.png';
import image2 from '../../../assets/images/totem-great-2.png';
import image3 from '../../../assets/images/totem-great-3.png';

import style from './style.module.scss'

function ImageGallery() {

    return (<>
        <div className={style.page}>
            <h1 className={style.immersiveHeader}>Great things happen when we work together</h1>
            <h2 className={style.subtitle}>Together we drive <b>higher engagement</b>, create <b>deeper learning</b>, and <b>increase retention</b>.</h2>
            <Row className={style.imageContainer}>
                <Col className={style.image}>
                    <img src={image1}/>
                    <h2>Through a scenario based game Totem Learning were able to increase productivity, reduce waste, build confidence and increase customer satisfaction.</h2>
                </Col>
                <Col className={style.image}>
                    <img src={image2}/>
                    <h2>Through micro elearning modules combined with real time behavioural tracking Totem Learning were able to reduce all at risk, distracted driving and speeding events. </h2>
                </Col>
                <Col className={style.image}>
                    <img src={image3}/>
                    <h2>Through simulation Totem Learning were able to train 85% of skills required for welding as well as reduce waste significantly. </h2>
                </Col>
            </Row>
        </div>
        </>);
}

export default ImageGallery;
